.superInput {
    appearance: none;
    display: block;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.superInput:focus {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 25%);
}

.errorInput {
    border: 1px solid red;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.errorInput:focus {
    border-color: red;
    box-shadow: 0 0 0 0.2rem rgb(191 26 26 / 25%);
}

.error {
    color: red;
}

.blue {
    background: blue;
}